* {margin: 0; padding: 0;}

body {
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    background-color: #111111;
    color: #ffffff;
}

iframe {
    border: none;
    border-radius: 10px;
}

a {
    color: #f2f2f2;
    text-decoration: none;
}

a:hover {
    text-decoration: underline;
}